import React from 'react';
import { Link } from "react-router-dom";

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';

import Logo from "../assets/logo.png";


const pages = ['Portfolio', 'About', 'Resume'];

const Header = ({style}) => {
    const [anchorElNav, setAnchorElNav] = React.useState(null);

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    return (
        <AppBar position="static" elevation={0} sx={{py: '1rem', px: '8vw'}} style={style}>
            <Container maxWidth="md" sx={{padding: "24px 0"}}>
                <Toolbar disableGutters>
                    {/* desktop logo view */}
                    
                    <Typography
                        variant="h1"
                        noWrap
                        component="div"
                        sx={{ flexGrow: 1, display: { xs: 'flex'} }}
                    >
                        <Link to="/" id="logo"><img class="small-img" src={Logo} /></Link>
                    </Typography>


                    {/* nav bar for desktop view */}
                    <Box 
                        sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, justifyContent: 'flex-end'
                        }}
                    >
                        <ul id="nav-links">
                            <li><Link to="/">Portfolio</Link></li>
                            <li><Link to="/about">About</Link></li>
                            <li><a target="_blank" rel="noreferrer" href="https://drive.google.com/file/d/1f2u27l64clmLZhe_IWjxmLoyrtivNp6w/view">Resume</a></li>
                        </ul>
                    </Box>

                    {/* side menu bar for mobile view */}
                    <Box sx={{ display: { xs: 'flex', md: 'none' }}}>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleOpenNavMenu}
                            color="default"
                            align="right"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                            }}
                            >

                            <div id="nav-links-mobile">
                            <MenuItem  onClick={handleCloseNavMenu}>
                                <Link to="/">Portfolio</Link>
                            </MenuItem>
                            </div>
                            <div id="nav-links-mobile">
                            <MenuItem onClick={handleCloseNavMenu}>
                                <Link to="/about">About</Link>
                            </MenuItem>
                            </div>
                            <div id="nav-links-mobile">
                            <MenuItem onClick={handleCloseNavMenu}>
                                <a rel="noreferrer" href="https://drive.google.com/file/d/1f2u27l64clmLZhe_IWjxmLoyrtivNp6w/view">Resume</a>
                            </MenuItem>
                            </div>
                        </Menu>
                    </Box>
                </Toolbar>
            </Container>
        </AppBar>
    );
};
export default Header;