import React, { Component } from 'react'

import Header from "../components/header";
import Footer from "../components/footer";

import Grid from '@mui/material/Grid';

import Profile from '../assets/profile.jpg';



const styles = {
  // general container
  mainContainer: {
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center'
  },

  // SECTIONS WITHIN CONTAINERS
  // general formatting
  generalSection: {
    flex: 1,
    maxWidth: 852,
    width: '100%',
    margin: '24px 10px',
    justifyContent: 'space-between'
  },
  // wider formatting
  largeSection: {
    flex: 1,
    maxWidth: 1052,
    width: '100%',
    padding: '24px 24px',
    justifyContent: 'center'
  },
}


class AboutMe extends Component {
  render() {
    return (
      <div class="color-bg">
        <Header style={{backgroundColor: 'transparent'}}/>
        <Grid container style={styles.mainContainer}>
        
        <div style={{ height: 90 }} />

        <Grid container item style={styles.largeSection}>
          <Grid item xs={12} sm={8} md={5} style={{padding: "2vh 20px 0 0"}}>
            <img src={Profile} alt="Profile image" style={{borderRadius: 10}}/>
          </Grid>

          <Grid item xs={12} md={7} style={{ padding: "2vh 20px"}}>
            <div style={{ height: 10 }} />

            <p class="large">Hi there , I’m Christina! I’m a <strong>product designer</strong> based in Orange County, CA.
              I value <strong>empathy, transparency, and growth.</strong></p>

            <p>I recently graduated from UCI (June 2022) as an Informatics major with a specialization in Human-Computer 
              Interaction. I am currently looking for an internship or full-time opportunities as a designer! </p>

            <p>Growing up, I’ve always been interested in getting my hands dirty with different crafts — polymer clay art, 
              sewing simple plush animals, and chalk art (just to name a few). Product design intrigued me since it <strong>allowed me to 
                apply my empathy and exercise my creative skills</strong> to design a solution that betters the experience of the people around me.</p>

            <p>When I’m not designing, I enjoy learning how to cook, trying new restaurants, crocheting, reading, and trying new crafts! 😊</p>
            


            {/* <p>📚 Education — University of California, Irvine</p>
            <p>🎨 Skills — User Research, Usability Testing, Personas, Storyboards, Sketching,
              Wireframing, Mockups, Prototyping, Visual Design, Typography, Competitive Analysis,
              Front-End Web Development</p>
            <p>💻 Coding — HTML, CSS, JavaScript, Python, C++, Basic Java</p>
            <p>🔨 Tools — Figma, Adoble XD, Adobe Photoshop</p>
            <p>💡 Currently Learning — Adobe Illustrator, Adobe Animate</p> */}

          </Grid>
        </Grid>
        </Grid>

        <div style={{ height: 100 }} />

        <Grid container style={styles.mainContainer}>
          <Grid item xs={12} style={styles.generalSection}>
            <Footer />
          </Grid>
        </Grid>
      </div>

    )
  }
}

export default AboutMe;