import React from "react";
import Grid from "@mui/material/Grid";
import Container from '@mui/material/Container';

import Persona from "../assets/persona3.png";

const styles = {
    heroContainer: {
        width: `100%`,
        alignContent: 'space-evenly',
        alignItems: 'center',
        justifycontent: 'space-evenly',
        justifyItems: 'center',
    }
};

const Intro = () => {
    return (
        <Grid sx={{flex: 1}}>
            <Container maxWidth="md">

                {/* Desktop view */}
                <Grid container item sx={{alignItems: 'center', marginTop: 12, display: { xs: 'none', md: 'flex' } }}>
                    <Grid item xs={7} >
                        <h1 class="intro">hi, i'm <strong id="purple-text">christina</strong> —</h1>
                        <h2 class="intro"><strong>product designer</strong> based in orange county, ca</h2>
                    </Grid>
                    <Grid item xs={5}>
                        <img class="med-img" src={Persona} alt="Drawing of Christina" />
                    </Grid>
                </Grid>

                {/* Mobile view */}
                <Grid container item direction="column" alignItems="center" sx={{ justifyContent: "center", display: { xs: 'flex', md: 'none' } }}>
                    <Grid item align="center">
                        <img class="small-img" src={Persona} alt="Drawing of Christina" />
                    </Grid>
                    <Grid item sx={{px: '8vw'}}>
                        <h1 class="intro">hi, i'm <strong id="purple-text">christina</strong></h1>
                        <h2 class="intro"><strong>product designer</strong> based in orange county, ca</h2>
                    </Grid>
                </Grid>

            </Container>
        </Grid>
        )
};
export default Intro;